import React from 'react' ;
import s from './Dialogs.module.css';
import DialogListContainer from './DialogList/DialogList';
import ActDialogContainer from './ActDialog/ActDialogContainer';

  
const Dialogs = (props) => {
   
    
    
    return(
        <div className={s.dialogs}>
         <div className={s.DialogList}><DialogListContainer /></div>
          <div className={s.ActDialog}><ActDialogContainer /></div>
        </div>
    )
};


export default Dialogs;











